// ** Third-party libs
import PropTypes from "prop-types";

// ** Third-party components
import {
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from "reactstrap";
import { ChevronDown } from "react-feather";

// ** Styles
import "./CustomSelectDropdown.scss";

const CustomSelectDropdown = ({
  label,
  options,
  useCustomDropdownItem,
  CustomDropdownItem,
  onLabelClick,
  onOptionClick,
  ...containerProps
}) => {
  return (
    <UncontrolledDropdown
      tag="li"
      className="mr-25 ml-1 h-100 d-flex align-items-center justify-content-center"
      {...containerProps}
    >
      <DropdownToggle
        tag="span"
        className="label-container font-weight-bold d-flex align-items-center justify-content-center"
        style={{ color: "#323759" }}
        href="/"
        onClick={(event) => {
          event.preventDefault();
          onLabelClick(event);
        }}
      >
        {label}
        <ChevronDown size={14} className="ml-1" />
      </DropdownToggle>
      <DropdownMenu tag="div" right className="dropdown-container">
        {useCustomDropdownItem ? (
          <CustomDropdownItem />
        ) : (
          options.map((option, index) => (
            <DropdownItem
              className="d-flex"
              tag="div"
              header
              key={index}
              // onClick={(event) => {
              //   // event.preventDefault();
              //   console.log(index);
              //   onOptionClick(event);
              // }}
            >
              {option}
            </DropdownItem>
          ))
        )}
      </DropdownMenu>
    </UncontrolledDropdown>
  );
};

export default CustomSelectDropdown;

CustomSelectDropdown.propTypes = {
  label: PropTypes.node,
  options: PropTypes.arrayOf(PropTypes.node),
  onLabelClick: PropTypes.func,
  onOptionClick: PropTypes.func,
};

CustomSelectDropdown.defaultProps = {
  label: "",
  options: [],
  useCustomDropdownItem: false,
  onLabelClick: () => {},
  onOptionClick: () => {},
};
