// ** Third-party libs
import PropTypes from "prop-types";

// ** Third-party components
import ReactPaginate from "react-paginate";
import { ArrowLeft, ArrowRight } from "react-feather";

// ** Styles
import "./CustomPagination.scss";

function CustomPagination({ totalPages, currentPage, setCurrentPage }) {
  return (
    <ReactPaginate
      forcePage={currentPage}
      onPageChange={(page) => setCurrentPage(page.selected)}
      pageCount={totalPages}
      nextLabel={<ArrowRight />}
      breakLabel="..."
      previousLabel={<ArrowLeft />}
      pageRangeDisplayed={4}
      marginPagesDisplayed={0}
      activeLinkClassName="pageItemActive"
      pageLinkClassName="pageItem"
      nextLinkClassName="pageItem navigate next"
      previousLinkClassName="pageItem navigate prev"
      breakLinkClassName="pageItem"
      containerClassName="pagination react-paginate justify-content-center align-items-center pr-1 mt-1"
    />
  );
}

export default CustomPagination;

CustomPagination.propTypes = {
  totalPages: PropTypes.number,
  currentPage: PropTypes.number,
  setCurrentPage: PropTypes.func,
  paginationHandler: PropTypes.func,
};

CustomPagination.defaultProps = {
  totalPages: 1,
  currentPage: 0,
  paginationHandler: () => {},
};
