import React from "react";
import { FormattedMessage } from "react-intl";
import Select from "../components/CustomSelect";
import { injectIntl } from "react-intl";

function ResJuridicalFilter({
    juridicalData,
    selectedJuridicalStat,
    setSelectedJuridicalStat,
    intl
}) {
    function mapper (data) {
        return ({
            key: data.metaDataId,
            value: data.metaDataName,
        })
    }

    return (
        <div className="px-1 pt-3">
            <div className="font-weight-bolder pb-1">
                <FormattedMessage id="juridical" />
            </div>
            <Select
                label={<FormattedMessage id="paperType" />}
                placeholder={<FormattedMessage id="selectPaperType" />}
                options={juridicalData?.papers?.map(mapper)}
                selectedOption={selectedJuridicalStat?.paper}
                onOptionChange={(value) =>
                    setSelectedJuridicalStat({ ...selectedJuridicalStat, paper: value })
                }
            />
            <Select
                label={<FormattedMessage id="juridicalStatus" />}
                placeholder={<FormattedMessage id="selectJuridicalStatus" />}
                options={juridicalData?.juridical?.map(mapper)}
                selectedOption={selectedJuridicalStat?.juridical}
                onOptionChange={(value) =>
                    setSelectedJuridicalStat({ ...selectedJuridicalStat, juridical: value })
                }
            />
        </div>
    );
}

export default injectIntl(ResJuridicalFilter);
