import React from "react";
import { FormattedMessage } from "react-intl";
import Select from "../components/CustomSelect";
import RangeInput from "../components/CustomRangeInput";

function ResHouseFilter ({ selectedHouseStat, onHouseStatChange }) {

    return (
        <div className="px-1 pt-3">
            <div className="font-weight-bolder pb-1">
                <FormattedMessage id="houseStat" />
            </div>
            <Select
                label={<FormattedMessage id="houseFloors" />}
                options={[1, 2, 3, 4, 5, 6, 7]}
                placeholder={<FormattedMessage id="selectNoOfFloors" />}
                selectedOption={selectedHouseStat?.floors}
                onOptionChange={(value) =>
                    onHouseStatChange({
                        ...selectedHouseStat,
                        floors: value,
                    })
                }
            />
            <Select
                label={<FormattedMessage id="houseBedRooms" />}
                options={[1, 2, 3, 4, 5, 6, 7]}
                placeholder={<FormattedMessage id="selectNoOfBedRooms" />}
                selectedOption={selectedHouseStat?.bedrooms}
                onOptionChange={(value) =>
                    onHouseStatChange({
                        ...selectedHouseStat,
                        bedrooms: value,
                    })
                }
            />
            <Select
                label={<FormattedMessage id="houseToilets" />}
                options={[1, 2, 3, 4, 5, 6, 7]}
                placeholder={<FormattedMessage id="selectNoOfToilets" />}
                selectedOption={selectedHouseStat?.wcs}
                onOptionChange={(value) =>
                    onHouseStatChange({
                        ...selectedHouseStat,
                        wcs: value,
                    })
                }
            />
            <RangeInput
                label={<FormattedMessage id="houseFloorArea" />}
                placeholder={{
                    start: "0",
                    end: "9999",
                }}
                valueUnit="m2"
                value={{
                    start: selectedHouseStat?.floorArea?.start,
                    end: selectedHouseStat?.floorArea?.end,
                }}
                onChange={{
                    start: (value) =>
                        onHouseStatChange({
                            ...selectedHouseStat,
                            floorArea: {
                                start: value,
                                end: selectedHouseStat?.floorArea?.end,
                            },
                        }),
                    end: (value) =>
                        onHouseStatChange({
                            ...selectedHouseStat,
                            floorArea: {
                                start: selectedHouseStat?.floorArea?.start,
                                end: value,
                            },
                        }),
                }}
            />
        </div>
    );
}

export default ResHouseFilter;
