// group properties into group
function groupProperties(
    listResponseObjects,
    columns,
    groups
    ) {

    const listResults = [];

    if (listResponseObjects && listResponseObjects.length > 0) {
        for (let id = 0; id < listResponseObjects.length;) {
            const groupObj = {};
            const result = {};
            const responseObject = listResponseObjects[id];
    
            columns.forEach((col, index) => {
                groupObj[col] = groups[index];
            });
            
            // Format group match with col: [property1, property2],
            Object.keys(groupObj).forEach(group => {
                result[group] = {};
        
                groupObj[group].forEach(col => {
                    result[group][col] = responseObject[col];
                });
            });
    
            if (result) {
                listResults.push(result);
                id++;
            }
        }
    }

    return listResults;
}

export default { groupProperties }