import React from "react";
import Input from "../components/CustomRangeInput";
import Select from "../components/CustomSelect";
import { FormattedMessage } from "react-intl";
import { injectIntl } from "react-intl";

function ResLandFilter ({
    landData,
    selectedLandStat,
    setSelectedLandStat,
    intl
}) {
    function mapper (data) {
        return {
            key: data.metaDataId,
            value: data.metaDataName,
        }
    }

    return (
        <div className="px-1 pt-3">
            <div className="font-weight-bolder pb-1">
                <FormattedMessage id="landStat" />
            </div>
            <Input
                label={<FormattedMessage id="landLength" />}
                valueUnit="m"
                placeholder={{
                    start: "0",
                    end: "99999",
                }}
                value={{
                    start: selectedLandStat?.length?.start,
                    end: selectedLandStat?.length?.end,
                }}
                onChange={{
                    start: (value) =>
                        setSelectedLandStat({
                            ...selectedLandStat,
                            length: {
                                ...selectedLandStat?.length,
                                start: value,
                            },
                        }),
                    end: (value) =>
                        setSelectedLandStat({
                            ...selectedLandStat,
                            length: {
                                ...selectedLandStat?.length,
                                end: value,
                            },
                        }),
                }}
            />
            <Input
                label={<FormattedMessage id="landWidth" />}
                valueUnit="m"
                placeholder={{
                    start: "0",
                    end: "99999",
                }}
                value={{
                    start: selectedLandStat?.width?.start,
                    end: selectedLandStat?.width?.end,
                }}
                onChange={{
                    start: (value) =>
                        setSelectedLandStat({
                            ...selectedLandStat,
                            width: {
                                ...selectedLandStat?.width,
                                start: value,
                            },
                        }),
                    end: (value) =>
                        setSelectedLandStat({
                            ...selectedLandStat,
                            width: {
                                ...selectedLandStat?.width,
                                end: value,
                            },
                        }),
                }}
            />
            <Input
                label={<FormattedMessage id="landArea" />}
                valueUnit="m2"
                placeholder={{
                    start: "0",
                    end: "99999",
                }}
                value={{
                    start: selectedLandStat?.area?.start,
                    end: selectedLandStat?.area?.end,
                }}
                onChange={{
                    start: (value) =>
                        setSelectedLandStat({
                            ...selectedLandStat,
                            area: {
                                ...selectedLandStat?.area,
                                start: value,
                            },
                        }),
                    end: (value) =>
                        setSelectedLandStat({
                            ...selectedLandStat,
                            area: {
                                ...selectedLandStat?.area,
                                end: value,
                            },
                        }),
                }}
            />
            <Select
                label={<FormattedMessage id="landShape" />}
                placeholder={intl.formatMessage({ id: "selectLandShape" })}
                options={landData?.shapes?.map(mapper)}
                selectedOption={selectedLandStat?.shape}
                onOptionChange={(value) => setSelectedLandStat({ ...selectedLandStat, shape: value })}
            />
        </div>
    );
}

export default injectIntl(ResLandFilter);
