import React from "react";
import { Row, Col, Select } from "antd";

import "antd/dist/antd.css";
import "./styles/select.scss";
import { normalizeString, removeSpace } from "../../../../helper/string"

const { Option } = Select;

function CustomSelect({
    label = "Select label",
    placeholder = "Select option",
    options = [
        {
            key: null,
            value: "",
        },
    ],
    selectedOption = null,
    onOptionChange = () => { },
    isInput = false,
}) {
    function filterOption (input, option) {
        const inp = removeSpace(normalizeString(input));
        const opt = removeSpace(normalizeString(option.children));
        return opt.indexOf(inp) >= 0;
    }

    return (
        <Row className="pb-1">
            <Col span={8} className="my-auto">
                {label}
            </Col>
            <Col span={16}>
                <Select
                    className="w-100"
                    placeholder={placeholder}
                    {...(isInput ? {
                        showSearch: true,
                        optionFilterProp: "children",
                        filterOption,
                    } : {})}
                    value={selectedOption}
                    onChange={onOptionChange}
                    allowClear
                >
                    {options.map((option, index) =>
                        typeof option === "string" ||
                        typeof option === "number" ? (
                            <Option key={index} value={option}>
                                {option}
                            </Option>
                        ) : (
                            option.key && (
                                <Option key={index} value={option.key || option}>
                                    {option.value || option}
                                </Option>
                            )
                        )
                    )}
                </Select>
            </Col>
        </Row>
    );
}

export default CustomSelect;
