import { REQUEST_CONFIG, REQUEST_METHOD } from "../constants/request";
import requestService from "./request";
import { removeEmptyProperties } from "../helper/object";
import { isEmptyObject } from "jquery";
import { notification } from "antd";
import { resListResponse } from "../constants/response";
import { ENDPOINTS } from "@constants/endpoints";
import { isObjEmpty, omit } from "../utility/Utils";

const FILTER_OBJECT_PAGE_SIZE = 20;

export default class RealEstateServices {

  static async getList(
    requestConfig = REQUEST_CONFIG,
    filterObject,
    page = 1
  ) {
    // handle empty request configurations
    removeEmptyProperties(requestConfig);

    // handle empty filter properties
    if (!isEmptyObject(filterObject)) {
      removeEmptyProperties(filterObject);
    }

    // handle pagination for api request
    const skip = page * FILTER_OBJECT_PAGE_SIZE;
    const body = {
      skip,
      limit: FILTER_OBJECT_PAGE_SIZE,
      filter: {},
      ...filterObject,
    };

    const response = await requestService
      .send({ ...requestConfig, data: body })
      .then(
        (res = resListResponse) =>
          !res?.error && {
            data: res.data?.data,
            totalRecord: res.data?.total,
            totalPage: Math.ceil(res.data?.total / FILTER_OBJECT_PAGE_SIZE),
          }
      )
      .catch((err) => {
        notification.error("Error", err.message);
      });

    return response;
  }

  static async getResDetail(id) {
    if (!id) return
    const response = await requestService
      .send({
        method: 'post',
        path: ENDPOINTS.GET_DATA_FOR_EDITING_BY_ID,
        data: { id: Number(id) }
      })
    return response?.data
  }

  static async updateRes(payload) {
    if(!isObjEmpty(payload)){
      const body = omit(payload)
      const update = await requestService.send({
        method: REQUEST_METHOD.POST,
        path: ENDPOINTS.SUBMIT_RES,
        data: body
      })
      return update ? true : false
    }
    return false
  }
}
