import React from "react";
import { Row, Col, Input } from "antd";
import { FormattedMessage } from "react-intl";
import { ReactComponent as ArrowRight } from "@src/assets/images/icons/arrow-right.svg";

function CustomRangeInput({
  label = "Input label",
  valueUnit = "",
  placeholder = {
    start: "Start placeholder",
    end: "End placeholder",
  },
  value = {
    start: null,
    end: null,
  },
  onChange = {
    start: () => {},
    end: () => {},
  },
}) {
  return (
    <Row className="pb-1">
      {/* Label of this row */}
      <Col span={8} className="d-flex align-items-center">
        {label}
      </Col>

      {/* Input of this row */}
      <Col span={16}>
        <Row>
          {/* Input for start value */}
          <Col span={10}>
            <Row>
              <FormattedMessage id="from" />
            </Row>
            <Row>
              <Input
                type="number"
                addonAfter={valueUnit}
                placeholder={placeholder.start}
                value={value.start}
                onChange={({ target: { value } }) => onChange.start(parseInt(value) || null)}
              />
            </Row>
          </Col>

          {/* Arrow icon */}
          <Col
            span={4}
            className="d-flex justify-content-center align-items-end pb-1"
          >
            <Row>
              <ArrowRight />
            </Row>
          </Col>

          {/* Input for end value */}
          <Col span={10}>
            <Row>
              <FormattedMessage id="to" />
            </Row>
            <Row>
              <Input
                type="number"
                addonAfter={valueUnit}
                placeholder={placeholder.end}
                value={value.end}
                onChange={({ target: { value } }) => onChange.end(parseInt(value) || null)}
              />
            </Row>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}

export default CustomRangeInput;
