import React from "react";
import { FormattedMessage } from "react-intl";
import Select from "../components/CustomSelect";

function ResTypeFilter({
    selectedResType,
    setSelectedResType,
    resTypeOptions,
}) {
    function mapper(data) {
        return ({
            key: data.realEstateCategoryId,
            value: data.realEstateCategoryName,
        });
    }
    return (
        <div className="px-1 pt-1">
            <div className="font-weight-bolder pb-1">
                <FormattedMessage id="resType" />
            </div>
            <Select
                label={<FormattedMessage id="resType" />}
                placeholder={<FormattedMessage id="selectTypeOfRes" />}
                options={resTypeOptions?.map(mapper)}
                selectedOption={selectedResType}
                onOptionChange={setSelectedResType}
            />
        </div>
    );
}

export default ResTypeFilter;
