import requestService from "./request";
import { notification } from "antd";

import "antd/dist/antd.css";

export default class FilterService {
    static async getCities(country = 1) {
        const response = await requestService
            .send({
                method: "POST",
                path: `DataFilter/getAreaData`,
                data: {
                    areaParentId: country,
                },
            })
            .then((result) =>
                result.data.data.filter((item) => {
                    const isCity = item.areaDataType.toLowerCase() === "city";
                    const isSameCountry = item.areaParentId === country;
                    return isCity && isSameCountry;
                })
            )
            .catch((err) => {
                notification.error({ message: err.message });
                return err;
            });

        return response;
    }

    static async getDistricts(city = 9218) {
        const response = await requestService
            .send({
                method: "POST",
                path: `DataFilter/getAreaData`,
                data: {
                    areaParentId: city,
                },
            })
            .then((result) =>
                result.data.data.filter((item) => {
                    const isDistrict =
                        item.areaDataType.toLowerCase() === "district";
                    const isSameCity = item.areaParentId === city;
                    return isDistrict && isSameCity;
                })
            )
            .catch((err) => {
                notification.error({ message: err.message });
                return err;
            });

        return response;
    }

    static async getWards(district = 10873) {
        const response = await requestService
            .send({
                method: "POST",
                path: `DataFilter/getAreaData`,
                data: {
                    areaParentId: district,
                },
            })
            .then((result) =>
                result.data.data.filter((item) => {
                    const isWard = item.areaDataType.toLowerCase() === "ward";
                    const isSameDistrict = item.areaParentId === district;
                    return isWard && isSameDistrict;
                })
            )
            .catch((err) => {
                notification.error({ message: err.message });
                return err;
            });

        return response;
    }

    static async getPostTypes() {
        const response = await requestService
            .send({
                method: "POST",
                path: `DataFilter/getDataFilterRealEstate`,
                data: {
                    realEstatePostTypeId: 1,
                },
            })
            .then((result) => result.data.dataRealEstateType)
            .catch((err) => {
                notification.error({ message: err.message });
                return err;
            });

        return response;
    }

    static async getCategories() {
        const response = await requestService
            .send({
                method: "POST",
                path: `DataFilter/getDataFilterRealEstate`,
                data: {
                    realEstatePostTypeId: 1,
                },
            })
            .then((result) => {
                return result.data.dataRealEstateCategory;
            })
            .catch((err) => {
                notification.error({ message: err.message });
                return err;
            });
        return response;
    }

    static async getLandShapes() {
        const response = await requestService
            .send({
                method: "POST",
                path: `MetaData/getList`,
                data: {
                    filter: {
                        metaDataType: "SHAPENAME",
                    },
                },
            })
            .then((result) => {
                return result.data.data;
            })
            .catch((err) => {
                notification.error({ message: err.message });
                return err;
            });
        return response;
    }

    static async getPaperTypes () {
        const response = await requestService
            .send({
                method: "POST",
                path: `MetaData/getList`,
                data: {
                    filter: {
                        metaDataType: "LEGAL_PAPERS",
                    },
                },
            })
            .then((result) => {
                return result.data.data;
            })
            .catch((err) => {
                notification.error({ message: err.message });
                return err;
            });
        return response;
    }

    static async getJuridicalStatus () {
        const response = await requestService
            .send({
                method: "POST",
                path: `MetaData/getList`,
                data: {
                    filter: {
                        metaDataType: "LEGAL_STATUS",
                    },
                },
            })
            .then((result) => {
                return result.data.data;
            })
            .catch((err) => {
                notification.error({ message: err.message });
                return err;
            });
        return response;
    }
}
