import React from "react";
import { FormattedMessage } from "react-intl";
import Input from "../components/CustomInput";
import Select from "../components/CustomSelect";
import { injectIntl } from "react-intl";

function ResAddressFilter({
    addressData,
    selectedAddress,
    setSelectedAddress,
    intl
}) {
    function mapper (data) {
        return ({
            key: data.areaDataId,
            value: data.areaDataName,
        })
    }

    return (
        <div className="px-1 pt-3">
            <div className="font-weight-bolder pb-1">
                <FormattedMessage id="address" />
            </div>
            <Select
                label={<FormattedMessage id="province" />}
                placeholder={<FormattedMessage id="selectProvince" />}
                options={addressData?.provinces?.map(mapper)}
                selectedOption={selectedAddress?.province}
                onOptionChange={(value) =>
                    setSelectedAddress({ ...selectedAddress, province: value })
                }
                isInput={true}
            />
            <Select
                label={<FormattedMessage id="district" />}
                placeholder={<FormattedMessage id="selectDistrict" />}
                options={addressData?.districts?.map(mapper)}
                selectedOption={selectedAddress?.district}
                onOptionChange={(value) =>
                    setSelectedAddress({ ...selectedAddress, district: value })
                }
                isInput={true}
            />
            <Select
                label={<FormattedMessage id="ward" />}
                placeholder={<FormattedMessage id="selectWard" />}
                options={addressData?.wards?.map(mapper)}
                selectedOption={selectedAddress?.ward}
                onOptionChange={(value) =>
                    setSelectedAddress({ ...selectedAddress, ward: value })
                }
                isInput={true}
            />
            <Input
                label={<FormattedMessage id="street" />}
                placeholder={intl.formatMessage({ id: "enterStreet" })}
                value={selectedAddress?.details}
                onChange={(value) =>
                    setSelectedAddress({ ...selectedAddress, details: value })
                }
            />
        </div>
    );
}

export default injectIntl(ResAddressFilter);
